import React, { useEffect , useState} from "react";
import '../index.css';
import polmap from '../src/Poland-map.png';
import { Map_info } from "../config";

window.actId = '';

function PolskaHTMLMap() {

    //Get info about active area
    const [isHovering, setIsHovering] = useState(false);
    //var isHovering = false;

    const handleMouseOver = () => {
          window.actId = document.activeElement.id;
          setIsHovering(true);       
    };

    const handleMouseOut = () => {
      window.actId = '';        
      setIsHovering(false);
    };

    
    // Focus
    useEffect(() => {

        function myFunction() {
          var areas = document.getElementsByTagName("area");          
          
          for (var index = 0; index < areas.length; index++) {
            areas[index].addEventListener(
              "mouseover",
              function () {
                this.focus();                    
              },
              true              
              );

            areas[index].addEventListener(
              "mouseout",
              function () {
                window.actId = document.activeElement.id;
                this.blur();                
              },
              true
              );            
            }
            
          }
        myFunction();
      }, []);      

      // Create map area
      const createMap = () => {
          
      const listMenu = [];
  
      for (let i = 0; i < Map_info.length; i++) {
              listMenu.push(
                  <area onMouseOver={handleMouseOver} onMouseLeave={handleMouseOut} id={Map_info[i].id} shape={Map_info[i].shape} alt={Map_info[i].name} coords={Map_info[i].coord} href={Map_info[i].href} />
              )
      }
      return listMenu;
    }
      
      return (
        <div class="map-box">
            <img class="map" src={polmap} width="1188" alt="Poland map" usemap="#Poland_map_Map" />
            <map name="Poland_map_Map">
                {createMap()}
            </map>
            {isHovering && 
            <div className="popup" style={{top: Map_info[window.actId].position.top, left: Map_info[window.actId].position.left}}>
                <div>
                  <img src={Map_info[window.actId].herb} alt={Map_info[window.actId].name} width="100px" />
                </div>
                <div>
                    <h1>{Map_info[window.actId].name}</h1>
                    <div className="add_info">
                      <h2>Siedziba wojewody: </h2>
                      <h2>{Map_info[window.actId].capital}</h2>
                      <h2>Populacja: </h2>
                      <h2>{Map_info[window.actId].population}</h2>
                      <h2>Powierzchnia: </h2>
                      <h2>{Map_info[window.actId].square}</h2>                      
                    </div>                    
                </div>
            </div>}
        </div>
)}

export default PolskaHTMLMap;